import { useEffect, useState } from "react"
import { toast } from "react-toastify"
import { Button, Card, CardBody, Container, Form, FormFeedback, FormGroup, Input, Label, Table } from "reactstrap"
import Base from "../components/Base"
import { deleteDoctor, enableDisableDoctor, getAllDoctors, getAllSpecialization, getDoctorById, registerNewDoctor, updateDoctorByAdmin } from "../services/doctor-service"
import { getAlldoctors, registerNewSeller } from "../services/seller-service"
import { getUserByEmail } from "../services/user_service"

const Doctors=()=>{

    const [page,setPage]=useState({
        pageNumber:0,
        pageSize:10,
        lastPage:false,
        doctors:[]
    })
    var doctorUserId;

    const [sps , setSps]=useState([])

    

    const [doctorDto,setDoctorDto] = useState({
        id:0,
        specialization:{
            id:0
        },
        experiance:'',
        fee:'',
        about:'',
        address:{
            name:"",
                city:"",
                state:"",
                pincode:"",
                mobile:"",
                address:""
        },
        name:'',
        registrationNo:'',
        qualification:'',
        degree:'',
        verified:false


    })

    const resetDoctorDto=()=>{
        setDoctorDto({
            id:0,
        specialization:{},
        experiance:'',
        fee:'',
        about:'',
        address:{
            name:"",
                city:"",
                state:"",
                pincode:"",
                mobile:"",
                address:""
        },
        name:'',
        registrationNo:'',
        qualification:'',
        degree:'',
        verified:true
    
        })
    }

    const fetchUser=(event)=>{
        let email = event.target.value
        let fb = document.getElementById("emailFeedback")
        if(email.includes("@gmail.com")){
            getUserByEmail(email).then(data=>{
                doctorUserId=data.id
                console.log("dr ur id = "+data.id)
                console.log(data?.name)
                fb.innerHTML=data?.name
                fb.style.color="green"
            }).catch(error=>{
                   // doctorUserId=-1
                    console.log(error)
                    fb.innerHTML="Not a Homeorx User"
                    fb.style.color="red"
                    //sellerUserId=-1
            })
        }else{
            console.log("invalid email")
            fb.innerHTML="Invalid Email"
            fb.style.color="red"
        }
        
    }

    let f=false;
    const showHide=()=>{
        let frame = document.getElementById("sellerForm");
        if(!f){
            frame.style.display="block"
            f=true
        }else{
            frame.style.display="none"
            f=false
        }
    }

    const updateDoctorDto=(event)=>{
        setDoctorDto({
            ...doctorDto,
            [event.target.id]: event.target.value
        })

    }
    const updateDoctorAddress=(event)=>{
        setDoctorDto({
            ...doctorDto,
           address:{
               ...doctorDto.address,
                [event.target.id]:event.target.value
           }
        })
    }

    const loadAlldoctors=(pageNumber=0,pageSize=5,specialization=-1)=>{
        getAllDoctors(pageNumber,pageSize,specialization).then(data=>{
            console.log(data)
            setPage(data)
        })
    }

    const validateDoctor=()=>{
        if(doctorDto.name.length<3){
            return false
       // }else if(!doctorDto.email.includes("@gmail.com")){
         //   return false
        }else if(doctorDto.address.address.length<3){
            return false
        }else if(doctorDto.address.city.length<3){
            return false
        }else if(doctorDto.address.state.length<3){
            return false
        }else if(doctorDto.address.pincode.length<6){
            return false
        }else if(doctorDto.address.mobile.length<10){
            return false
        }else if(doctorDto.fee<0){
            return false
        }else if(doctorDto.specialization.id==0){
            toast.error("please select specialization")
            return false
        }else if(doctorDto.specialization.id==undefined){
            toast.error("please select specialization")
            return false
        }else if(doctorUserId==undefined){
            toast.error("not a valid email id")
            return false
        }
        return true
    }
    useEffect(()=>{
        loadAlldoctors()
        getAllSpecialization().then(data=>{
            setSps(data)
        }).catch(error=>console.log(error))
    },[])

    const updateDoctor=()=>{
        updateDoctorByAdmin(doctorDto).then(data=>{
            toast.success("Doctor Updated Successfully!!!")
            loadAlldoctors()
            resetDoctorDto()
        }).catch(error=>{
            console.log(error)
        })
    }
    const fetchDoctorById=(drId)=>{
        showHide()
        getDoctorById(drId).then(data=>{
            setDoctorDto(data)
        }).catch(error=>console.log(error))
    }

    const enableDisable=(drId)=>{
            enableDisableDoctor(drId).then(data=>{
                toast.success("Changes Successfully !!!")
                loadAlldoctors()
            }).catch(error=>console.log(error))
    }

    const deleteThisDoctor=(drId)=>{
        deleteDoctor(drId).then(data=>{
            toast.success(data)
            loadAlldoctors()
        }).catch(error=>{console.log(error)
            toast.error("Something Went Wrong !!!")
        })
    }
    const addNewDoctor=()=>{
        if(!validateDoctor()){
            toast.error("Please fill all the details...")
            return
        }
        showHide()
        console.log("id ="+doctorUserId)
        console.log("sps ="+doctorDto.specialization.id);
        console.log(doctorDto)
        
        registerNewDoctor(doctorDto,doctorUserId).then(data=>{
            toast.success("Doctor Registered Successfully!!!")
            loadAlldoctors()
            resetDoctorDto()
        }).catch(error=>{
            console.log(error)
        })
        
        
    }

    return (
        <Base>
        <Container>
            <Card className="my-1 rounded-0 p-1">
            <i class="fas fa-user-tag fa-2x text-warning"> doctors <Button onClick={()=>showHide()} className="btn-small btn-info">Add New</Button></i> 
            </Card>

            <Card className="mb-1" id="sellerForm" style={{display:'none'}}>
                <CardBody>
                <Form>
                    <FormGroup>
                        <Input onChange={(event)=>fetchUser(event)} placeholder="User Email" id="email"  type="text" />
                        <small className="ml-1" id="emailFeedback"></small>
                    </FormGroup>
                    <FormGroup>
                        <Input placeholder="Doctor Id" id="id" disabled value={doctorDto.id} type="number" />
                    </FormGroup>
                    <FormGroup>
                    <Input defaultValue="1" id="specialization" onChange={(event)=>setDoctorDto({...doctorDto,specialization:{id:event.target.value}})} type="select" >
                                        <option selected> ---Select Category--- </option>
                                        {
                                            sps.map((category,index)=>(
                                                <option key={index}  value={category.id}  >{category.name}</option>
                                            ))
                                        }
                                        
                                    </Input>
                    </FormGroup>
                    <FormGroup>
                        <Input placeholder="Name " id="name" onChange={(event)=>updateDoctorDto(event)} value={doctorDto.name} type="text" />
                    </FormGroup>
                    <FormGroup>
                        <Input placeholder="Fee " id="fee" onChange={(event)=>updateDoctorDto(event)} value={doctorDto.fee} type="number" />
                    </FormGroup>
                    <FormGroup>
                        <Input placeholder="Experiance" id="experiance" onChange={(event)=>updateDoctorDto(event)} value={doctorDto.experiance} type="text" />
                    </FormGroup>
                    <FormGroup>
                        <Input placeholder="Registration NO if any" id="registrationNo" onChange={(event)=>updateDoctorDto(event)} value={doctorDto.registrationNo} type="text" />
                    </FormGroup>
                    <FormGroup>
                        <Input placeholder="Qualification like MBBS" id="qualification" onChange={(event)=>updateDoctorDto(event)} value={doctorDto.qualification} type="text" />
                    </FormGroup>
                    <FormGroup>
                        <Input placeholder="Degree if any, Drive url only" id="degree" onChange={(event)=>updateDoctorDto(event)} value={doctorDto.degree} type="text" />
                    </FormGroup>

                    <FormGroup>
                        <Label>Clinic/Personal Address</Label>
                        <Input placeholder="Name / Clinic Name" id="name" onChange={(event)=>updateDoctorAddress(event)} value={doctorDto.address.name} type="text" />
                        <Input placeholder="Address Line 1" id="address" onChange={(event)=>updateDoctorAddress(event)} value={doctorDto.address.address} type="text" />
                        <Input className="mt-1" placeholder="city" id="city" onChange={(event)=>updateDoctorAddress(event)} value={doctorDto.address.city} type="text" />
                        <Input className="mt-1" placeholder="state" id="state" onChange={(event)=>updateDoctorAddress(event)} value={doctorDto.address.state} type="text" />
                        <Input className="mt-1" placeholder="PIN Code" id="pincode" onChange={(event)=>updateDoctorAddress(event)} value={doctorDto.address.pincode} type="number" />
                        <Input className="mt-1" placeholder="mobile no" id="mobile" onChange={(event)=>updateDoctorAddress(event)} value={doctorDto.address.mobile} type="number" />
                    </FormGroup>
                    <Container className="text-center">
                    <Button onClick={()=>updateDoctor()} className="btn-primary rounded-0 ml-1" type="reset">Update</Button>
                        <Button onClick={()=>addNewDoctor()} className="btn-warning rounded-0" type="button">Add New Doctor</Button>
                        <Button onClick={()=>resetDoctorDto()} className="btn-primary rounded-0 ml-1" type="reset">Reset</Button>
                    </Container>



                </Form>
                </CardBody>
            </Card>

            <Card>
            <Table
            responsive
            striped
            >
            <thead>
                <tr>
                <th>
                    #Id
                </th>
                <th>
                    Name
                </th>
                <th>
                    Address
                </th>
                <th>
                    Specialization
                </th>
                <th>
                    Fee
                </th>
                <th>
                    Verified
                </th>
                <th>
                    Action
                </th>
                </tr>
            </thead>
            <tbody>
                
                {
                    page.doctors.map((doctor,i)=>(
                    
                    <tr key={i}>
                        <th className="myHover" scope="row">
                    {doctor.id}
                </th>
                <td>
                    {doctor.name}
                </td>
                <td>
                    {doctor.address?.city}
                </td>
                <td>
                    {doctor?.specialization?.name}
                </td>
                <td>
                    {doctor.fee}
                </td>
                <td>
                    <Form>
                        <FormGroup switch>
                        <Input type="switch" onClick={()=>enableDisable(doctor.id)} checked={doctor.verified} />
                        </FormGroup>
                    </Form>
                </td>
                <td>
                    <a onClick={()=>fetchDoctorById(doctor.id)} className="btn-primary"><i class="fas fa-edit"></i></a> 
                    <a className="text-danger" onClick={()=>deleteThisDoctor(doctor.id)} style={{marginLeft:"10px"}} ><i class="fas fa-trash-alt"></i></a>
                </td>
                </tr>
            
                    
                    )
                    )
                }
            
            </tbody>
            </Table>
            </Card>
            
        </Container>
        </Base>
    )
}

export default Doctors