import { useLocation, useNavigate, useParams } from "react-router-dom"
import { Button, Card, CardText, CardTitle, Col, Container, Form, FormGroup, Input, Label, Row, Table } from "reactstrap"
import Base from "../components/Base"
import { useEffect, useState } from "react"
import Status from "../components/Status"
import { AddNewOrderStatus, DeleteOrderStatus, UpdateAddress, UpdateDeliveryAmount, UpdateOrderBox, UpdateOrderNotes, UpdateOrderPayment } from "../services/order-service"
import { toast } from "react-toastify"

const OrderUpdate=()=>{
    let navigate=useNavigate()
    const [order,setOrder] =useState(useLocation().state)
    
    useEffect(()=>{
        console.log(order)

    },[])

    const updateAmount=()=>{
        let amount =document.querySelector("#amount").value
        let deliveryCharge =document.querySelector("#deliverycharge").value
        UpdateDeliveryAmount(deliveryCharge,amount,order.orderId).then(data=>{
            setOrder(data)
            toast.success("Address Updated Successfully !!!")
            console.log(data)
        }).catch(error=>console.log(error))
    }

    const updateAddress=()=>{
        UpdateAddress(order.address,order.orderId).then(data=>{
            setOrder(order)
            toast.success("Address Updated Successfully !!!")
            console.log(data)
        }).catch(error=>console.log(error))
    }

    const deleteOrderStatus=(sid)=>{
        const statusId = JSON.parse(sid)
        DeleteOrderStatus(statusId,order.orderId).then(data=>{
            setOrder({...order,status:data})
            toast.info("Status Deleted Successfully !!!")
        }).catch(error=>console.log(error))
    }

    const addOrderStatus=()=>{
        const st =document.querySelector("#status").value
        AddNewOrderStatus(st,order.orderId).then(data=>{
            setOrder({...order,status:data})
            toast.success(st+" Added Successfully as Status")
        }).catch(error=>console.log(error))
    }

    const updateNotes=()=>{
        UpdateOrderNotes(order.notes,order.orderId).then(data=>{
            setOrder(data)
            toast.success("Notes Updated Successfully !!!")
        }).catch(error=>console.log(error))
    }

    const updatePayment=()=>{
        const paymentType = document.querySelector("#paymentType").value
        const paymentVerified = document.querySelector("#paymentVerified").value
        UpdateOrderPayment(paymentType,paymentVerified,order.orderId).then(data=>{
            setOrder(data)
            toast.info("payment details updated successfully !!!")
        }).catch(error=>console.log(error))
    }

    const updateBox=()=>{
        const weight =document.querySelector("#weight").value
        const height = document.querySelector("#height").value
        const length = document.querySelector("#length").value
        const breadth = document.querySelector("#breadth").value

        UpdateOrderBox(weight,length,breadth,height,order.orderId).then(data=>{
            setOrder(data)
            toast.info("Box details updated successfully !!!")
        }).catch(error=>console.log(error))
    }

    return(
        <Base>
            <Container>
                
                <Card body className="mt-2">
                    {"#"+order.orderId}
                    <CardTitle tag="h5">
                        Update Order  {order.ordertype}
                    </CardTitle>


                   <Row className="mt-2">
                       <Col md="3">
                       <Form>
                    <FormGroup>
                        <Label for="exampleEmail">
                        Update Address
                        </Label>
                        <Input
                        id="name"
                        name="text"
                        placeholder="Customer name"
                        value={order?.address?.name}

                        onChange={(event)=>setOrder({...order,address:{...order.address,[event.target.id]:event.target.value}})}
                        type="text"
                        />
                    </FormGroup>
                    <FormGroup>    
                        <Input
                        id="mobile"
                        name="mobile"
                        placeholder="Mobile No"
                        value={order?.address?.mobile}
                        type="number"
                        onChange={(event)=>setOrder({...order,address:{...order.address,[event.target.id]:event.target.value}})}
                        />
                    </FormGroup>
                    <FormGroup>    
                        <Input
                        id="city"
                        name="city"
                        placeholder="City"
                        value={order?.address?.city}
                        type="text"
                        onChange={(event)=>setOrder({...order,address:{...order.address,[event.target.id]:event.target.value}})}
                        />
                    </FormGroup>
                    <FormGroup>    
                        <Input
                        id="state"
                        name="state"
                        placeholder="State"
                        value={order?.address?.state}
                        type="text"
                        onChange={(event)=>setOrder({...order,address:{...order.address,[event.target.id]:event.target.value}})}
                        />
                    </FormGroup>
                    <FormGroup>    
                        <Input
                        id="pincode"
                        name="pincode"
                        placeholder="PIN Code"
                        value={order?.address?.pincode}
                        type="number"
                        onChange={(event)=>setOrder({...order,address:{...order.address,[event.target.id]:event.target.value}})}
                        />
                    </FormGroup>
                    <FormGroup>    
                        <Input
                        id="address"
                        name="address"
                        placeholder="Complete Address"
                        value={order?.address?.address}
                        type="textarea"
                        onChange={(event)=>setOrder({...order,address:{...order.address,[event.target.id]:event.target.value}})}
                        cols="4"

                        />
                    </FormGroup>
                    <Button type="button" onClick={()=>updateAddress()} outline>Update</Button>
                    </Form>
                       </Col>
                    <Col md="4" className="offset-md-3">
                        <Card body>
                            <CardTitle tag="h5">Address</CardTitle>
                            <CardText>
                            {order.address.name}
                            <br />
                            {order.address.mobile}
                            <br />
                            {order.address.address}
                            <br />
                            <b>City : </b>
                            {order.address.city}
                            <br />
                            <b>State : </b>
                            {order.address.state} 
                            <br />
                            <b>PIN Code : </b>
                            {order.address.pincode}
                            </CardText>
                        </Card>
                    </Col>
                   </Row>

                   {/* for amount */}

                   <Row className="mt-5">
                       <Col md="3">
                       <Form>
                       <FormGroup row>
                            <Label for="amount" sm={6}>Delivery Charge</Label>
                            <Col sm={6}>
                            <Input
                                id="deliverycharge"
                                name="deliverycharge"
                                placeholder="delivery charge"
                                type="number"
                                 />
                            
                            </Col>
                        </FormGroup>
                       <FormGroup row>
                            <Label for="amount" sm={6}>Tota Amount</Label>
                            <Col sm={6}>
                            <Input
                                id="amount"
                                name="amount"
                                placeholder="with a placeholder"
                                type="number"
                                />
                            </Col>
                        </FormGroup>
                        <Button type="button" onClick={()=>updateAmount()} outline>Update</Button>
                        </Form>
                       </Col>

                    <Col md="4" className="offset-md-3">
                        <Card body>
                            <Table borderless>
                                <tbody>
                                    <tr>
                                        <td>Delivery Charge</td>
                                        <td>{order.deliverycharge}</td>
                                        
                                    </tr>
                                    <tr><td colSpan={2}><hr /></td></tr>
                                    <tr>
                                        <td>Total</td>
                                        <td>{order?.amount}</td>
                                    </tr>
                                </tbody>
                            </Table>
                     
                           
                        </Card>
                    </Col>
                   </Row>


                   {/* for status */}

                   <Row className="mt-5">
                       <Col md="4">
                       <table>{
                            order?.status?.map((status,i)=>(
                                <>
                                <tr>
                                <td className="status-td"><p className="status-circle" >{i+1}</p></td>
                                <td><p className="status-text">{status.status} <small className='text-warning'>{status.date}</small></p>
                                 
                                </td>
                                <td><Button onClick={()=>deleteOrderStatus(JSON.stringify(status.id))} type="button" outline color="danger">Delete</Button></td>
                                
                                </tr>
                            <tr>
                                <td className="status-line"><i class="fas fa-ellipsis-v text-warning"></i></td>
                            </tr>
                            </>

                            ))
                            }
                        <tr>
                            
                        </tr>
                        </table>
                        <Form>
                            <FormGroup row>
                                <Col md="9">
                                <Input type="select" name="status" id="status">
                                    <option value={"PLACED"}>PLACED</option>
                                    <option value={"WAITING FOR CONFIRMATION"}>WAITING FOR CONFIRMATION</option>
                                    <option value={"PROCESSING"}>PROCESSING</option>
                                    <option value={"OUT FOR PICKUP"}>OUT FOR PICKUP</option>
                                    <option value={"PICKUPED"}>PICKUPED</option>
                                    <option value={"OUT FOR DELIVERY"}>OUT FOR DELIVERY</option>
                                    <option value={"DELIVERED"}>DELIVERED</option>
                                    <option value={"NOT DELIVERED"}>NOT DELIVERED</option>
                                    <option value={"DELIVERED"}>WAITING FOR PAYMENT</option>
                                    <option value={"PAYMENT COMPLETED"}>PAYMENT COMPLETED</option>
                                    <option value={"CANCELLED"}>CANCELLED</option>
                                    
                                </Input>
                                </Col>
                                <Col md="3">
                                    <Button type="button" onClick={()=>addOrderStatus()} outline color="success">Add</Button>
                                </Col>
                            </FormGroup>
                                
                            </Form>
                       </Col>

                    <Col md="4" className="offset-md-2">
                        <Card body>
                            <Status props={order.status} />
                        </Card>
                    </Col>
                   </Row>

                   {/* Notes  */}

                   <Row className="my-5">
                        <Col md="3">
                            <Form>
                                <FormGroup>
                                    <Input type="textarea" rows="3" id="notes" value={order.notes} onChange={(event=>setOrder({...order,"notes":event.target.value}))}></Input>
                                </FormGroup>
                                <Button type="button" outline color="success" onClick={()=>updateNotes()}>Submit</Button>
                            </Form>

                        </Col>
                        <Col className="offset-md-3" md="4">

                                <Card body>
                                    <CardTitle tag="h5">Notes</CardTitle>
                                    <CardText>
                                        {order?.notes}
                                    </CardText>
                                </Card>
                        </Col>
                   </Row>
                
                {/* payment  */}

                   <Row className="my-3">
                            <Col md="3">
                            <Form>
                            <FormGroup row>
                            <Label md="3" for="paymentType">MODE :</Label>
                                <Col md="9">
                                <Input type="select" name="status" id="paymentType">
                                    <option value={"PREPAID"}>PREPAID</option>
                                    <option value={"COD"}>Cash On Delivery (COD)</option>   
                                </Input>
                                </Col>
                                
                            </FormGroup>
                            <FormGroup row>
                            <Label md="3" for="paymentType">Status :</Label>
                                <Col md="9">
                                <Input type="select" name="status" id="paymentVerified">
                                    <option value={1}>VERIFIED</option>
                                    <option value={0}>PENDING</option>   
                                </Input>
                                </Col>
                                
                            </FormGroup>
                            <Button type="button" onClick={()=>updatePayment()} outline color="success">Submit</Button>
                            </Form>

                            </Col>
                            <Col md="4" className="offset-md-3">
                                <Card body>
                                    <CardTitle tag="h5">Payment</CardTitle>
                                    <CardText>
                                        <b>MODE : </b>{order?.paymentType}
                                        <br />
                                        <b>Status : </b>{ order.paymentVerified?"Verified":"Pending" }
                                    </CardText>
                                </Card>
                            </Col>
                        </Row>

                         {/* Delivery Package  */}

                   <Row className="my-3">
                            <Col md="3">
                            <Form>
                            <FormGroup row>
                            <Label md="5" for="paymentType">Weight (Kg)</Label>
                                <Col md="7">
                                    <Input type="text" id="weight" />
                                </Col>
                            </FormGroup>
                            <FormGroup row>
                            <Label md="5" for="paymentType">Length (cms)</Label>
                                <Col md="7">
                                    <Input type="text" id="length" />
                                </Col>   
                            </FormGroup>
                            <FormGroup row>
                            <Label md="5" for="paymentType">Breadth (cms)</Label>
                                <Col md="7">
                                    <Input type="text" id="breadth" />
                                </Col>   
                            </FormGroup>
                            <FormGroup row>
                            <Label md="5" for="paymentType">Height (cms)</Label>
                                <Col md="7">
                                    <Input type="text" id="height" />
                                </Col>   
                            </FormGroup>
                            <Button type="button" onClick={()=>updateBox()} outline color="success">Submit</Button>
                            </Form>

                            </Col>
                            <Col md="4" className="offset-md-3">
                                <Card body>
                                    <CardTitle tag="h5">Shipping Box</CardTitle>
                                    <i class="fas fa-box-open fa-4x text-warning"></i>
                                    <CardText>
                                        <b>Weight : </b>{order?.weight} Kg
                                        <br />
                                        <b>Length : </b>{ order.length } cms
                                        <br />
                                        <b>Breadth : </b>{ order.breadth } cms
                                        <br />
                                        <b>Height : </b>{ order.height } cms
                                    </CardText>
                                </Card>0
                            </Col>
                        </Row>
                </Card>
            </Container>
        </Base>
    )
}

export default OrderUpdate