import { useEffect, useState } from "react"
import { useNavigate, useParams } from "react-router-dom"
import { Button, Card, CardBody, CardHeader, CardText, CardTitle, Col, Container, Modal, ModalBody, ModalFooter, ModalHeader, NavItem, Row, Table } from "reactstrap"
import Base from "../components/Base"
import { toast } from "react-toastify";
import { BASE_URL, DRIVE_IMAGE_URL } from "../services/helper"
import { GetOrderById, SendCancelOrderStatus, SendCancelPickupRequest, SendDeliveredOrderStatus, SendOutForDeliveryOrderStatus, SendPickupRequest, TrackOrderStatus } from "../services/order-service"
import Status from "../components/Status";

const Order=()=>{
    let navigate=useNavigate()
    const {orderId}=useParams()
    const [order,setOrder]=useState({
        "status":[]
    });

    const [pickupmodal, setPickupodal] = useState(false);

    const pickuptoggle = () => setPickupodal(!pickupmodal);
    const [cancelmodal, setCancelmodal] = useState(false);

    const canceltoggle = () => setCancelmodal(!cancelmodal);

    const loadOrder=()=>{
        GetOrderById(Number(orderId)).then((data)=>{
            setOrder(data)
            console.log(data)
        }).catch(error=>{
            console.log(error)
        })
        console.log(order)
    }

    useEffect(()=>{
        loadOrder()
    },[])

    const requestPickup=()=>{
        pickuptoggle()
        SendPickupRequest(orderId).then(data=>{
            if(data.status_code==1)
                toast.success("Order Pickup Requested Successfully !!!")
            console.log(data)
            loadOrder()
        }).catch(error=>{
            toast.error("Sorry something went wrong !!!")
            console.log(error)
        })
    }
    const cancelPickupRequest=()=>{
        SendCancelPickupRequest(orderId).then(data=>{
            toast.success("Pickup Request Cancelled Successfully !!!")
            console.log(data)
            loadOrder()
        }).catch(error=>{
            toast.error("Sorry something went wrong !!!")
            console.log(error)
        })
    }
    //cancel order status only
    const cancelOrder=()=>{
        SendCancelOrderStatus(orderId).then(data=>{
            toast.success("Order Cancelled Successfully")
            loadOrder()
        }).catch(error=>{
            toast.error("Sorry Something went wrong")
            console.log(error)
        })
    }
    const outForDelivery=()=>{
        SendOutForDeliveryOrderStatus(orderId).then(data=>{
            toast.success("OUT FOR DELIVERY status changed successfully")
            loadOrder()
        }).catch(error=>{
            console.log(error)
            toast.error("Sorry Something Went Wrong")
        })


    }
    const deliveredOrderStatus=()=>{
        SendDeliveredOrderStatus(orderId).then(data=>{
            toast.success("DELIVERED status changed successfully")
            loadOrder()
        }).catch(error=>{
            console.log(error)
            toast.error("Sorry Something Went Wrong")
        })
    }

    const trackOrder=()=>{
        if(order.trackingId ==null || order.trackingId ==""){
            toast.error("Please Wait For the Order Pickup")
            return
        }
            
        TrackOrderStatus(order.orderId).then(data=>{
            window.location.href=data
        }).catch(error=>console.log(error))
    }

    return(
        <Base>

            <Container className="mt-2">
                <Card>
                    <CardHeader className="" style={{background:"none"}}> 
                        <p style={{margin:"0px"}}>Order ID : <b> {order?.orderId} </b></p>
                        <p style={{margin:"0px"}} className="" >Status : <b> {order?.status[order.status.length-1]?.status}</b></p>
                        <small>{order.date}</small>
                    
                    </CardHeader>
                    <CardBody>
                    
                    <p className="text-center cursor"><b><u>Item details</u></b></p>
                        <Table responsive>
                        <tbody>
                            <tr>
                                <th>Item number</th>
                                <th>Item name</th>
                                <th>Item quantity</th>
                                <th>Item price</th>
                            </tr>
                            {
                            order?.items?.map((item,i)=>(
                                <tr key={i}>
                                <td>{item.id}</td>
                                <td style={{whiteSpace:"nowrap"}} > <img width="60px" src={DRIVE_IMAGE_URL+item.product?.images[0]?.name} alt="" />{item.product?.name} </td>
                                <td>X {item.quantity}</td>
                                <td style={{whiteSpace:"nowrap"}} >₹ {item.price}</td>
                            </tr>
                            ))
                            }
                            <tr>
                                <td></td>
                                <td>Delivery Charge</td>
                                <td></td>
                                <td style={{whiteSpace:"nowrap"}} >₹ {order.deliverycharge}</td>
                            </tr>
                            <tr>
                                <td></td>
                                <td><b>Total price</b></td>
                                <td></td>
                                <td style={{whiteSpace:"nowrap"}}><b>₹ {order.amount}</b></td>
                            </tr>
                            <tr>
                                <td>Order Type</td>
                                <td>{order.ordertype}</td>
                            </tr>
                            
                        </tbody>
                        </Table>
                        <Row className="my-3">
                            <Col md="5">
                            <Card body>
                                <p onClick={()=>trackOrder()} style={{textAlign:"right",cursor:"pointer",color:"blue",marginBottom:"-20px"}}><i class="fas fa-map-marker-alt"> Track Order</i></p>
                            <Status props={order.status} />
                        </Card>
                            </Col>
                            <Col className="offset-md-3" md="4">
                            <Container className="my-3">
                        <Button onClick={()=>pickuptoggle()} className="rounded-0" outline color="success" style={{marginRight:"10px",width:"100%"}}>Pickup Request</Button>
                        <br  />
                        <Button onClick={()=>canceltoggle()} className="rounded-0" outline color="danger" style={{marginRight:"10px",marginTop:"5px",width:"100%"}}>Cancel Pickup Request</Button>
                        <br  />
                        
                        <Button onClick={()=>navigate("/order_update",{state:order})} className="rounded-0" outline color="warning" style={{width:"100%",marginTop:"5px"}} >Update</Button>
                        </Container>
                            </Col>
                        </Row>
                        
                        <Row className="mt-3">
                            <Col md="4">
                            <Card body>
                            <CardTitle tag="h5">Address</CardTitle>
                            <CardText>
                            {order?.address?.name}
                            <br />
                            {order?.address?.mobile}
                            <br />
                            {order?.address?.address}
                            <br />
                            <b>City : </b>
                            {order?.address?.city}
                            <br />
                            <b>State : </b>
                            {order?.address?.state} 
                            <br />
                            <b>PIN Code : </b>
                            {order?.address?.pincode}
                            </CardText>
                        </Card>
                            </Col>
                            <Col className="offset-md-5" md="3">
                            
                              <img className="myHover" onClick={()=>window.location.href=BASE_URL+"images/prescriptions/"+order.prescription} width={200} src={BASE_URL+"images/prescriptions/"+order.prescription} alt="" />
                            </Col>
                        </Row>

                        {/* payment details */}
                        <Row className="mt-3">
                            <Col md="4">
                            <Card body>
                            <CardTitle tag="h5">Payment</CardTitle>
                            <CardText>
                                        <b>MODE : </b>{order?.paymentType}
                                        <br />
                                        <b>Status : </b>{ order.paymentVerified?"Verified":"Pending" }
                                    </CardText>
                        </Card>
                            </Col>
                        </Row>
                        
                        <Row className="mt-3">
                        <Col md="4" className="">
                                <Card body>
                                    <CardTitle tag="h5">Shipping Box</CardTitle>
                                    <i class="fas fa-box-open fa-4x text-warning"></i>
                                    <CardText>
                                        <b>Weight : </b>{order?.weight} Kg
                                        <br />
                                        <b>Length : </b>{ order.length } cms
                                        <br />
                                        <b>Breadth : </b>{ order.breadth } cms
                                        <br />
                                        <b>Height : </b>{ order.height } cms
                                    </CardText>
                                </Card>
                            </Col>
                        </Row>

                        <Row className="mt-3">
                        <Col md="4" className="">
                                <Card body>
                                    <CardTitle tag="h5">Notes</CardTitle>
                                    
                                    <CardText>
                                        {order.notes}
                                    </CardText>
                                </Card>
                            </Col>
                        </Row>
                        
                        
                    </CardBody>
                </Card>



                <Modal isOpen={pickupmodal} toggle={()=>pickuptoggle()} >
        <ModalHeader toggle={()=>pickuptoggle()}>Are you sure want to pickup request ?</ModalHeader>
        <ModalBody>
        <Card body>
                                    <CardTitle tag="h5">Shipping Box</CardTitle>
                                    <i class="fas fa-box-open fa-4x text-warning"></i>
                                    <CardText>
                                        <b>Weight : </b>{order?.weight} Kg
                                        <br />
                                        <b>Length : </b>{ order.length } cms
                                        <br />
                                        <b>Breadth : </b>{ order.breadth } cms
                                        <br />
                                        <b>Height : </b>{ order.height } cms
                                    </CardText>
                                </Card>

                           
          
        </ModalBody>
        <ModalFooter>
          <Button color="success" outline onClick={()=>requestPickup()}>
            Confirm Pickup Request
          </Button>{' '}
          <Button color="secondary" outline onClick={()=>pickuptoggle()}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal>


       <Modal isOpen={cancelmodal} toggle={()=>canceltoggle()} >
        <ModalHeader toggle={()=>canceltoggle()}>Cancel Pickup Request ?</ModalHeader>
        <ModalBody>
                <p>Are you Sure want to cancel order pickup</p>
      </ModalBody>
        <ModalFooter>
          <Button color="warning" outline onClick={()=>cancelPickupRequest()}>
            Cancel Pickup Request
          </Button>{' '}
          <Button color="secondary" outline onClick={()=>canceltoggle()}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal>
            </Container>
        </Base>
    )

}

export default Order