import { privateAxios } from "./helper"

export const GetAllorders=()=>{
    let url = "admin/orders"
    return privateAxios.get(url).then(response=>response.data)
}

export const GetAllProductorders=(pageNumber,pageSize)=>{
    let url = `admin/all_orders?pageNumber=${pageNumber}&pageSize=${pageSize}`
    return privateAxios.get(url).then(response=>response.data)
}

export const GetOrderById=(orderId)=>{
    let url = `admin/orders/order/${orderId}`
    return privateAxios.get(url).then(response=>response.data)
}

export const SendPickupRequest=(orderId)=>{
    let url = `admin/orders/pickup/order/${orderId}`
    return privateAxios.post(url).then(response=>response.data)
}
export const SendCancelPickupRequest=(orderId)=>{
    let url = `admin/orders/cancel_pickup/order/${orderId}`
    return privateAxios.post(url).then(response=>response.data)
}
export const SendCancelOrderStatus=(orderId)=>{
    let url = `admin/orders/cancel/order/${orderId}`
    return privateAxios.put(url).then(response=>response.data)
}
export const SendOutForDeliveryOrderStatus=(orderId)=>{
    let url = `admin/orders/out_for_delivery/order/${orderId}`
    return privateAxios.put(url).then(response=>response.data)
}
export const SendDeliveredOrderStatus=(orderId)=>{
    let url = `admin/orders/delivered/order/${orderId}`
    return privateAxios.put(url).then(response=>response.data)
}

export const UpdateDeliveryAmount=(deliveryCharge,amount,orderId)=>{
    let url = `order/${orderId}?amount=${amount}&deliveryCharge=${deliveryCharge}`
    return privateAxios.put(url).then(response=>response.data)
}

export const UpdateAddress=(address,orderId)=>{
    let url = `order/address/${orderId}`
    return privateAxios.put(url,address).then(response=>response.data)
}

export const DeleteOrderStatus=(statusId,orderId)=>{
    let url = `status/${orderId}?statusId=${statusId}`
    return privateAxios.delete(url).then(response=>response.data)
}

export const AddNewOrderStatus=(status,orderId)=>{
    let url = `status/${orderId}?status=${status}`
    return privateAxios.post(url).then(response=>response.data)
}

export const UpdateOrderNotes=(notes,orderId)=>{
    let url = `order/notes/${orderId}`
    return privateAxios.put(url,{"username":notes,"password":""}).then(response=>response.data)
}

export const TrackOrderStatus=(orderId)=>{
    let url = `order/tracking/${orderId}`
    return privateAxios.put(url).then(response=>response.data)
}

export const UpdateOrderPayment=(paymentType,paymentVerified,orderId)=>{
    let url = `order/payment/${orderId}?paymentType=${paymentType}&paymentVerified=${paymentVerified}`
    return privateAxios.put(url).then(response=>response.data)
}

export const UpdateOrderBox=(weight,length,breadth,height,orderId)=>{
    let url = `order/box/${orderId}?weight=${weight}&length=${length}&breadth=${breadth}&height=${height}`
    return privateAxios.put(url).then(response=>response.data)
}