import { myAxios, privateAxios } from "./helper"

export const getAllSpecialization=()=>{
    return privateAxios.get("users/specializations").then(response=>response.data)
}
export const registerNewDoctor=(doctorDto,userId)=>{
    return privateAxios.post(`doctor/register/${userId}`,doctorDto).then(response=>response.data)
}
export const getDoctorById=(drId)=>{
    return privateAxios.get(`doctor/doctor/${drId}?doctorId=${drId}`).then(response=>response.data)
}
export const updateDoctorByAdmin=(doctorDto)=>{
    return privateAxios.put(`doctor/update_doctor/`,doctorDto).then(response=>response.data)
}
export const enableDisableDoctor=(doctorId)=>{
    return privateAxios.put(`doctor/enable_disable/${doctorId}`).then(response=>response.data)
}
export const deleteDoctor=(doctorId)=>{
    return privateAxios.delete(`doctor/doctor/${doctorId}`).then(response=>response.data)
}
export const getAllDoctors=(pageNumber,pageSize,specialization)=>{
    return privateAxios.get(`doctor/doctors?pageNumber=${pageNumber}&pageSize=${pageSize}&specialization=${specialization}`).then(response=>response.data)
}
export const getAllAppointments=(pageNumber,pageSize)=>{
    return privateAxios.get(`doctor/all_appointments?pageNumber=${pageNumber}&pageSize=${pageSize}`).then(response=>response.data)
}

export const getAllPrescriptionsOfPatient=(pageNumber,pageSize,patientId)=>{
    return privateAxios.get(`prescription/?pageNumber=${pageNumber}&pageSize=${pageSize}&patientId=${patientId}`).then(response=>response.data)
}

export const getAppointById=(appId)=>{
    return privateAxios.get(`doctor/appointment/${appId}`).then(response=>response.data)
}

export const updateAppointByAadmin=(apt)=>{
    return privateAxios.put(`doctor/appointment_update`,apt).then(response=>response.data)
}

export const UploadNewPrescription=(presc,patientId)=>{
    return privateAxios.post(`prescription/add/${patientId}`,presc).then(response=>response.data)
}

export const UpdatePrescription=(presc)=>{
    return privateAxios.put(`prescription/`,presc).then(response=>response.data)
}

export const ScheduleAppointMeeting=(app)=>{
    return privateAxios.put(`appointment/schedule`,app).then(response=>response.data)
}

export const addSpecialization=(specialization)=>{
    return privateAxios.post("admin/add_specialization",specialization).then(response=>response.data)
}

