import { useLocation, useParams } from "react-router-dom"
import { Card, CardImg, CardText, Col, Container, Form, FormGroup, Input, Label, Row } from "reactstrap"
import Base from "../components/Base"
import { useEffect, useState } from "react"
import { getAllPrescriptionsOfPatient, UpdatePrescription, UploadNewPrescription } from "../services/doctor-service"
import { BASE_URL, privateAxios } from "../services/helper"
import { toast } from "react-toastify"
import InfiniteScroll from "react-infinite-scroll-component"

const Prescriptions=()=>{
    const {ptId} = useParams()
    const [presc,setPresc] = useState({
        lastPage:"",
        prescriptions:[]
    })
    const [currentPage,setCurrentPage] = useState(0)
    const [patient,setPatient]= useState(useLocation().state)
    const [prescription,setPrescription] = useState({
        createdDate:"",
        image:"",
        disease:"",
        symptoms:"",
        notes:"",
        nextDate:""
    })

    const [selectedFile, setSelectedFile] = useState()
    const [preview, setPreview] = useState()

    // create a preview as a side effect, whenever selected file is changed
    useEffect(() => {
        if (!selectedFile) {
            setPreview(undefined)
            document.querySelector("#preImage").style.display="block"
            document.querySelector("#newImage").style.display="none"
            return
        }
        document.querySelector("#preImage").style.display="none"
        document.querySelector("#newImage").style.display="block"
        const objectUrl = URL.createObjectURL(selectedFile)
        setPreview(objectUrl)

        // free memory when ever this component is unmounted
        return () => URL.revokeObjectURL(objectUrl)
    }, [selectedFile])

    const onSelectFile = e => {
        if (!e.target.files || e.target.files.length === 0) {
            setSelectedFile(undefined)
            return
        }

        // I've kept this example simple by using the first image instead of multiple
        setSelectedFile(e.target.files[0])
    }

    const updatePrescriptionImage=(pId)=>{
        //console.log(appointment)
        const form = document.forms.namedItem("form2")
        const formData = new FormData(form)
       // formData.append("prescription_image",document.getElementById("prescription_image").)
        privateAxios.post(`prescription/image/${pId}`,formData,{headers:{
            'Content-Type':'multipart/form-data',
        }}).then(response=>{
            //console.log(response.data)
            toast.success("Saved Successfully !!!")
           // loadAppointment()
        }).catch(error=>console.log(error))
        
    }




    const updateApp=(event)=>{
        setPrescription({
            ...prescription,
            [event.target.id]:event.target.value
        })
    }

    const loadPrescriptions=()=>{
        getAllPrescriptionsOfPatient(currentPage,5,patient.id).then(data=>{
            //console.log("loading page ",currentPage)
            setPresc({
                pageNumber:data.pageNumber,
                pageSize:data.pageSize,
                totalElements:data.totalElements,
                totalPages:data.totalPages,
                lastPage:data.lastPage,
                prescriptions:[...presc.prescriptions,...data.prescriptions]})
           // console.log(presc)
        }).catch(error=>console.log(error))
    }

    useEffect(()=>{
        loadPrescriptions()
        
    },[currentPage])

    useEffect(()=>{
        console.log("page data",presc)
    },[presc])

    const getThePrescription=(pres)=>{
        const prescr = JSON.parse(pres)
        setPrescription(prescr)
    }

    const updatePrescription=()=>{
        UpdatePrescription(prescription).then(data=>{
            setPrescription(data)
            toast.info("prescription updated successfully !!!")
            if(selectedFile !=null){
                updatePrescriptionImage(data.id).then(data=>{
                    //loadPrescriptions()
                   // setCurrentPage(0)
                    toast.success("New prescription image updated successfully !!!")
                }).catch(error=>console.log(error))
            }
        }).catch(error=>console.log(error))
    }

    const uploadNewPrescription=()=>{
        console.log(prescription)
        UploadNewPrescription(prescription,patient.id).then(data=>{
            setPrescription(data)
            setCurrentPage(0)            
            toast.info("New prescription uploaded successfully !!!")
            if(selectedFile !=null){
               
                updatePrescriptionImage(data.id).then(data=>{
                    
                    
                    toast.success("prescription image uploaded successfully !!!")
                }).catch(error=>console.log(error))
            }
            loadPrescriptions()
            
        }).catch(error=>console.log(error))
    }


    return (
        <Base>
            <Container>
                <Row>
                <Col md="3">
                    <div className="bg-warning p-2" style={{height:"63px"}}>
                    <p className="m-0 "><i class="fas fa-notes-medical fa-2x text-success myHover" ></i> All Prescriptions</p>
                    </div>
                    <div id="scrollableDiv" className="p-1" style={{height:"720px",overflowX:"auto",overflowY:"scroll"}} >


                    <InfiniteScroll
                        dataLength={presc.prescriptions.length}
                        hasMore={!presc.lastPage}
                        next={()=>{setCurrentPage(currentPage+1)}}
                        scrollableTarget="scrollableDiv"
                        
                        
                    >
                    {(presc.prescriptions).map(pr=>(
                        <div className="mt-3">
                        <hr style={{margin:"0px 0px -13px 0px"}} />
                        <small className="bg-white">{pr.createdDate}</small>
                        <Card onDoubleClick={()=>getThePrescription(JSON.stringify(pr))} className="myHover" body>
                        <img alt="Sample" height={150} src={BASE_URL+"images/prescriptions/"+pr.image} />
                        <p className="m-0"> <b>Disease : </b>{pr.disease} </p>
                        <p className="m-0"> <b>Symptoms : </b>{ pr.symptoms} </p>
                        <p className="m-0"> <b>Next Date : </b>{ pr.nextDate} </p>
                        <CardText> {pr.notes}</CardText>
                         </Card>
                        </div>

                        ))}
                    </InfiniteScroll>


                            

                    </div>      

                </Col>
                <Col md="8">
                    <div className="bg-warning p-2">
                     <div className="d-flex">
                     <i class="fas fa-user-circle fa-3x"> </i>
                        <div className="pl-3" style={{paddingLeft:"8px"}}>
                        <p  style={{marginBottom:"-10px !important"}} className="m-0"> {patient.name}</p>
                        <small className="m-0">{patient.age} , {patient.sex}</small>
                        </div>
                     </div>
                    </div>
                    <div style={{height:"645px"}}>
                        <Card className="rounded-0 mt-1" body>
                            <Row>
                                <Col md="6">
                              <img  id="preImage" style={{width: "380px", height: "480px",paddingRight:"5px",display:"none"}} alt="Sample" src={BASE_URL+"images/prescriptions/"+prescription.image} />
                                
                                <img id="newImage" style={{width: "380px", height: "480px",paddingRight:"5px",display:"none"}} src={preview} /> 
                                </Col>
                                <Col md="6">

                                <Form >
                            <FormGroup>
                                <Label for="symptoms">Symptoms</Label>
                                <Input onChange={(event)=>updateApp(event)} type="textarea" id="symptoms" value={prescription?.symptoms}></Input>
                            </FormGroup>
                            <FormGroup>
                                <Label for="disease">Disease</Label>
                                <Input onChange={(event)=>updateApp(event)} type="textarea" id="disease" value={prescription?.disease}></Input>
                            </FormGroup>
                            <FormGroup>
                                <Label for="prescription">Prescription</Label>
                                <Input rows="6" onChange={(event)=>updateApp(event)}  type="textarea" id="notes" value={prescription?.notes}></Input>
                            </FormGroup>
                            
                            <FormGroup>
                                <Label for="nextDate">Next Date</Label>
                                <Input onChange={(event)=>updateApp(event)} id="nextDate" type="date" value={prescription?.nextDate} />
                            </FormGroup>

                        </Form>

                                </Col>
                            </Row>

                        </Card>

                    </div>

                    <div className="bg-warning p-2">
                        <Row className="p-0 m-0">
                            <Col md="1">
                            <i onClick={()=>updatePrescription()} class="fas fa-caret-square-up fa-2x myHover"></i>
                            </Col>
                            <Col md="10">
                            <Form name="form2" id="form2" encType="multipart/form-data">
                                
                                <Input onChange={(event)=>onSelectFile(event)} name="prescription_image" id="prescription_image" type="file"></Input>
                              
                                    
                                    {/* <Button className="btn btn-warning" onClick={()=>updatePrescriptionImage()} type="button">Submit</Button> */}
                                </Form>
                            </Col>
                            <Col md="1">
                            <i onClick={()=>uploadNewPrescription()} class="fas fa-paper-plane fa-2x myHover"></i>
                            </Col>
                        </Row>
                        
                    </div>
                </Col>
                </Row>
            </Container>
        
        </Base>
    )
}

export default Prescriptions