import { useEffect, useState } from "react"
import InfiniteScroll from "react-infinite-scroll-component"
import { useNavigate } from "react-router-dom"
import { toast } from "react-toastify"
import { Button, Card, CardBody, Col, Container, Row,Modal, ModalHeader, ModalBody, Form, FormGroup, Input, Label } from "reactstrap"
import Base from "../components/Base"
import { getAllAppointments, ScheduleAppointMeeting } from "../services/doctor-service"

const AllAppointments=()=>{
    let navigate = useNavigate()
    const [currentPage,setCurrentPage] = useState(0)
    const [pageData,setPageData] = useState({
        pageNumber:"",
        pageSize:"",
        totalElements:"",
        totalPages:"",
        lastPage:true,
        appointments:[]
    })

    const [modal, setModal] = useState(false);

    const toggle = () => setModal(!modal);


    const [appointment,setAppointment]=useState({
        id:0,
        scheduleTime:"",
        invitationLink:""
    })

    const scheduleMeeting=(appointment1)=>{
        const appointment2 =JSON.parse(appointment1)
        setAppointment(appointment2)
        toggle()


    }

    const updateMeeting=()=>{
        console.log("going data",appointment)
        toggle()
        ScheduleAppointMeeting(appointment).then(data=>{
            toast.success("appointment updated successfully !!!")
            toggle()
            console.log("updated data",data)
            getAllAppointments()
        }).catch(error=>console.log(error))
        console.log(appointment)
    }

    useEffect(()=>{
        getAllAppointments(0,5).then(data=>{
            console.log(data)
            setPageData(data)
        }).catch(error=>console.log(error))
    },[])

    useEffect(()=>{
        getAllAppointments(currentPage,5).then(data=>{
            setPageData({
                pageNumber:data.pageNumber,
                pageSize:data.pageSize,
                totalElements:data.totalElements,
                totalPages:data.totalPages,
                lastPage:data.lastPage,
                appointments:[...pageData.appointments,...data.appointments]})
        }).catch(error=>console.log(error))
    },[currentPage])
    return (
        <Base>
            
            <Container>
            <Card className="rounded-0 my-1 py-1">
            <i class="fas fa-headset fa-2x text-warning mx-2"> All Appointments</i>
            </Card>
            <InfiniteScroll
                        dataLength={pageData.appointments.length}
                        hasMore={!pageData.lastPage}
                        next={()=>{setCurrentPage(currentPage+1)}}
                        
                    >
                    {(pageData.appointments).map(ap=>(
                        <Card>
                            <CardBody>
                                <Row>
                                    <Col md="1">

                                            <Container className="text-center">
                                                <i class="fas fa-user-injured fa-2x text-primary"></i>
                                                <i class="fas fa-headset fa-2x text-primary mx-2"></i>
                                                <i class="fas fa-user-md fa-2x text-primary"></i>
                                                
                                                
                                            </Container>
                                        </Col>
                                        <Col md="2">
                                        <h5 className="text-success my-0">Dr. {ap.doctor?.name}</h5>
                                        <p className="my-0">{ap.doctor?.specialization?.name}</p>
                                        <p className="my-0"><i class="fas fa-map-marker-alt"></i>  {ap.doctor?.address?.city}</p>
                                        <p className="my-0"><i class="fas fa-phone-alt"></i> {ap.doctor?.address?.mobile}</p>
                                        <p onClick={()=>navigate("/prescriptions",{state:ap.patient})} className="my-0 myHover"><i class="fas fa-notes-medical fa-2x text-success"></i></p>
                                            
                                        </Col>
                                        <Col md="4">
                                            <div className="table-responsive">
                                            <table>
                                                <tbody>
                                                    <tr>
                                                        <td>Date</td>
                                                        <td>{ap.date}</td>
                                                    </tr>
                                                    <tr>
                                                        <td>Pt. Name</td>
                                                        <td>{ap.patient.name}</td>
                                                    </tr>
                                                    <tr>
                                                        <td>Mobile : </td>
                                                        <td><a href={`tel:${ap.mobile}`}> {ap.mobile} </a> </td>
                                                    </tr>
                                                   
                                                    <tr>
                                                        <td>Symptoms : </td>
                                                        <td> {ap.symptoms}</td>
                                                    </tr>
                                                    <tr>
                                                        <td>Mode : </td>
                                                        <td> {ap.type}</td>
                                                    </tr>
                                                </tbody>
                                            </table>
                                            </div>
                                         
                                        </Col>
                                        <Col md="3">
                                        <Button disabled style={{marginRight:"10px",width:"100%"}} className="btn-small rounded-0 btn-outline-warning">
                                            {ap?.paymentVerified && (<i class="fas fa-wallet text-success"> {ap?.status}</i>)}
                                            {!(ap?.paymentVerified) && (<i class="fas fa-wallet text-warning"> {ap?.status}</i>)}
                                        
                                        </Button>
                                        <Button onClick={()=>navigate("/appointment/"+ap.id)} style={{marginRight:"10px",width:"100%"}} className="btn-small rounded-0 btn-warning">View</Button>
                                        </Col>

                                        <Col md="2">
                                        {
                                            (ap.scheduleTime != null) && (<p onClick={()=>window.location.href=ap.invitationLink} className="fa-1x myHover"><i class="far fa-clock"></i> {ap.scheduleTime}</p>)
                                        }
                                            <Button onClick={()=>scheduleMeeting(JSON.stringify(ap))} outline color="success">Schedule Meeting</Button>
                                        </Col>
                                   
                                    
                                </Row>
                            </CardBody>

                        </Card>
                        
                    ))}
                    </InfiniteScroll>


                    <Modal isOpen={modal} toggle={()=>toggle()} >
                        <ModalHeader toggle={()=>toggle()}>Schedule Meeting with Patient</ModalHeader>
                        <ModalBody>
                            <Form>
                                <FormGroup>
                                    <Label>Schedule Time</Label>
                                    <Input value={appointment.scheduleTime} onChange={(event)=>setAppointment({...appointment,"scheduleTime":event.target.value})} type="datetime-local" />
                                </FormGroup>

                                <FormGroup>
                                    <Label>Invitation Link</Label>
                                    <Input value={appointment.invitationLink} onChange={(event)=>setAppointment({...appointment,"invitationLink":event.target.value})} type="text" />
                                </FormGroup>
                                <FormGroup>
                                    <Label>Select Doctor</Label>
                                    <Input type="select" >
                                        <option>dr1</option>
                                        <option>dr2</option>
                                    </Input>
                                </FormGroup>

                                
                                <Button type="button" onClick={()=>updateMeeting()} outline color="success" className="btn-block">Submit</Button>
                            </Form>
                        </ModalBody>
        
                    </Modal>

            </Container>
        </Base>
    )
}
export default AllAppointments